



















/*动画*/
.fade-enter-active[data-v-6dd8f44a],
.fade-leave-active[data-v-6dd8f44a] {
    -webkit-transition: all .2s linear;
    transition: all .2s linear;
    -webkit-transform: translate3D(0, 0, 0);
            transform: translate3D(0, 0, 0);
}
.fade-enter[data-v-6dd8f44a],
.fade-leave-active[data-v-6dd8f44a] {
    -webkit-transform: translate3D(100%, 0, 0);
            transform: translate3D(100%, 0, 0);
}


/*不限制图片大小，实现居中*/
img[data-v-6dd8f44a] {
    max-width: 100%;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1000;
}



